.prime {
    position: relative;
    
    
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 53%;
        height: 100%;
        background-image: linear-gradient(180deg, #6A6A6A 0%, #333333 100%);
        clip-path: polygon(0 0, 100% 0%, 74% 100%, 0% 100%);
        z-index: 1;
    }
    
    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 60%;
        height: 100%;
        background-image: url(../img/fon.png);
       
    }

    
    
}



.prime-screen {
    position: relative;
    z-index: 2;
    padding: 60px 0 173px;
    &__title {
        font-weight: 600;
        font-size: 42px;
        line-height: 50px;
        color: $white;
        max-width: 540px;
        font-family: $oswald;
    }

    &__btn {
        font-size: 16px;
        line-height: 19px;
        color: $white;
        padding: 13px 17px;
        background: $green;
        margin-top: 40px;
        border: 1px transparent solid;
        font-family: $roboto;
        
        &:hover {
            background: $white;
            color: $green;
            border-color: $green;
           
        }
    }

   &::after {
        content: "";
        position: absolute;
        width: 787px;
        height: 301px;   
        right: max(0px, (100% - 1140px) / 2);
        bottom: -69px;
        background-image: url(../img/auto.svg);
       z-index: 2;
    }

}


@include media-max($tablet){
    .prime{
        margin-top: 94px;
        &::before {
            width: 80%;
        }

        &::after {
            width: 63%;
        }

    }

    .prime-screen {
        &::after {
            background-image: url(../img/auto_768.png);
            width: 555px;
            height: 212px;
            bottom: -47px;
            
        }
    }

}

@include media-max(530px) {
  .prime-screen {
    &::after {
        display: none;
    }
  }  
}

@include media-max($mobile) {
    .prime-screen {
        &__title {
            font-size: 32px;
        }

        &__btn {
            padding: 13px 4px;
        }

        &::after {
            display: none;
        }
    }
}