.reviews {
    color: $black;
    padding: 86px 0 81px;
    &__title {
        font-weight: 600;
        font-size: 26px;
        line-height: 39px; 
        font-family: $oswald;     
    }
    &__txt {
        font-size: 18px;
        line-height: 21px;
        margin: 15px 0 60px;
        font-family: $roboto;
    }

}

.reviews-block {
    display: flex;
    justify-content: space-between;
    
    &__item {
        width: 350px;
        height: 480px;
        background-color: $white;
        padding: 24px 15px 38px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
        box-sizing: border-box;
    }
    &__title {
        font-size: 20px;
        line-height: 23px;
        margin-bottom: 15px;
        font-family: $roboto;
        font-weight: 700;
    }
    &__txt {
        font-size: 18px;
        line-height: 21px;
        font-family: $roboto;
    }
    &__info {
        font-size: 16px;
        line-height: 19px;
        font-style: italic;
        font-family: $roboto;
    }

}


.pagination {
    display: flex;
    justify-content: center;
    padding-top: 55px;
    z-index: 11;
    &__item {
        &:first-child {
            padding-right: 15px;
        }
        &:last-child {
            padding-left: 15px;
        }
    }
}

.swiper {
    width: 1110px;
    height: 300px;
  }


  @include media-max($mobile) {
    .reviews {
        padding-bottom: 60px;
        &__txt {
            margin-bottom: 36px;
        }
    }
  }