.header {
    font-family: $roboto;

    &__contact {
        background-color: $white;
        padding: 10px 0;

        &-content {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    

    &-menu {
        display: flex;
        justify-content: space-between;
        padding: 14px 0;
        font-weight: 400;
        font-size: 18.0488px;
        line-height: 21px;
    }
 
    &__burger {
        
        justify-content: space-between;
        align-items: center;
        
        
    }
}

.contact {
    
    &__company {
        display: flex;
        max-width: 480px;
    }

    &__slogan {
        margin-left: 35px;
        font-weight: 400;
        font-size: 18px;
        line-height: 21px;
    }

    &__item {
        display: flex;
        align-items: center;

        &--header {
            padding-left: 27px;
        }
        
        &:not(:last-child) {
            margin-bottom: 13px;
        }


        &:nth-child(1) > svg {
            top: -4px;
            left: -3px;
            position: absolute;  
        }
        &:nth-child(2) > svg {
            top: -3px;
            left: 0;
            position: absolute;
        }
        &:nth-child(3) > svg {
            position: absolute;
            top: 0;
            left: 0;
        }
        &:nth-child(4) > img {
            margin-right: 9px;    
        }
    }



}

.social {
    &__item:not(:last-child) {
            margin-right: 29px;   
    }
}


.just-burger {
    width: 45px;
    color: $green;
    z-index: 100;
}



@include media-max($tablet){
        .header {
            &__contact {
                padding: 0;
            }
            

            &__top {
                background-color: $white;
            }

            &__container-fixed  {
                position: fixed;
                width: 100%;
                z-index: 100;
                top: 0;
                left: 0;
                height: 95px;
            }

            &__bottom {
                display: block;
                
                transform: translateX(110%);
                transition: all 1s ease;
                position: relative;
                z-index: 10;
                &_relative {
                    background-color: $white;
                    max-width: 1110px;
                    padding-top: 70px;
                    position: relative;
                    top: -90px;
                    
                    
                }
            }
            &__burger {
                display: flex;
                padding: 25px 0;
            }
            &-menu {
                display: block;
                min-width: 100%;
                font-family: $oswald;
                font-weight: 600;
                font-size: 26px;
                line-height: 39px;
                padding: 0;
                flex-grow: 1;
                & > .menu-item{
                    margin-bottom: 30px;
                }
            }

            &__mobile-menu {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                max-width: 632px;
                padding-bottom: 300px;
                
            }
            
        }

        .contact {
            display: block;
            margin-right: 40px;
            margin-bottom: 50px;
            &__company {
                display: block;
            }
            &__slogan {
                margin-left: 0;
                margin-top: 15px;
            }
            &__contacts {
                margin: 30px 0 20px;
            }
        }
        

        .open {
            transform: translateX(0%);
           
            transition: all 1s lenear .5s;
          }
          
          
        
    }