.contacts-footer {
  font-family: $roboto;
  color: $white;
  height: 640px;
  position: relative;

  &__map {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;

  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 50%;
    height: 100%;
    background-color: #333333;
    opacity: 0.9;
    z-index: 1;
  }

  &__info {
    max-width: 370px;
    left: max(15px, (100% - 1140px) / 2);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    display: block;
    padding: 90px 0 137px;
    margin-right: max(320px, (100% - 1140px) / 2);
  }
}

#ymaps1656056863718812149 {
  height: 640px !important;
}

.ymaps-2-1-79-map {
  height: 640px !important;
}

.contact {


  &__company_block {
    display: block;
  }

  &__slogan_footer {
    margin-left: 0;
    font-style: normal;
    font-weight: 600;
    font-size: 26px;

    font-family: $oswald;
    margin: 20px 0 40px;
  }

  &__item {
    position: relative;

    &--footer {
      padding-left: 35px;
    }
    

    &>img {
      position: absolute;
      top: 0;
      left: 0;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }

  }

  &__phone {
    display: flex;
    flex-wrap: wrap;

    &>span {
      margin-bottom: 5px;

      &:nth-child(even) {
        margin-right: 60px;
      }
    }
  }

  &__title {
    margin: 50px 0 30px;
    font-size: 20px;
  }
}



.time {
  display: flex;
  flex-wrap: wrap;
  &__item {
    display: flex;
    flex-direction: column;

    &:not(:last-child) {
      margin-right: 30px;
    }
  }
}

.social__item {
  &_mr {
    &:not(:last-child) {
      margin-right: 15px;
    }
  }
}

@include media-max($tablet) {
  .contacts-footer {
    &::before {
      width: 52%;
    }
  }

  .contact {
    &__phone {
      &>span {

        &:nth-child(even) {
          margin-right: 30px;
        }
      }
    }
  }
}


@include media-max($mobile) {
  .contacts-footer {
    height: auto;
    background-color: #333333;

    &::before {
      display: none;
    }

    &__info {
      position: static;
      transform: none;
      padding: 40px 0 50px;
    }

    &__map {
      position: static;

    }
  }


  #ymaps1656056863718812149 {
    height: 520px !important;
  }
  .ymaps-2-1-79-map {
    height: 520px !important;
  }

  .contact {
    margin: 0;

    &__footer {
      font-size: 14px;
    }

    &__slogan_footer {
      max-width: 300px;
      line-height: 30px;
    }

    &__social {
      justify-content: start;
    }

    &__title {

      font-size: 16px;
    }
  }
}
