.footer {
  padding: 25px 0;
  background-color: $white;
  font-family: $roboto;
  &__contact {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-right {
    display: flex;

    &_column {
      flex-direction: column;
      justify-content: end;
    }
  }

&__social {
  display: flex;
}  

&__copyright {
    margin-top: 60px;  
    font-size: 14px;
}
&__politics {
    display: flex;
    justify-content: end;
    font-size: 14px;
    text-decoration: underline;
    margin-top: 60px;
}

}

.contact {
  &__company_footer {
    flex-direction: column;

    
  }

  &__btn {
    padding: 14px 14px;
    background-color: $green;
    color: $white;
    font-size: 16px;
    margin-left: 50px;
    border: 1px transparent solid;
    &:hover {
            background: $white;
            color: $green;
            border-color: $green;
           
        }
  }
}


.social {

  align-items: center;

  &__item_footer:not(:last-child) {
    margin-right: 18px;
  }


}



@include media-max($tablet) {
  .footer {

    &__contact {
      margin-right: 0;
    }
  }

  .contact {
    &__btn {
      margin-left: 75px;
    }
  }
}

@include media-max(530px) {
  .footer {
    padding: 40px 0 30px;
    &__contact {
      flex-direction: column;
      justify-content: center;
    }

    &-right {
      flex-direction: column;
      justify-content: center;
    }

    &__copyright {
    display: flex;
    justify-content: center;
    margin: 30px 0;
}
&__politics {
    margin-top: 0;
}

&__social {
    display: flex;
    justify-content: center;
  }
  }

  .contact {
    &__btn {
      margin: 40px 0 25px;
      padding: 14px 34px;
    }

    
  
}  
}

