
.form-screen {
    position: relative; 
    background-size: cover;
    width: 100%;
    
    
    &__wrapper {
        z-index: 1;
        position: relative;
        display: flex;
        justify-content: space-between;
        width: 100%;
    }
    
    &::before {
        content:"";
        background-color: $green;
        position: absolute;
        left: 0;
        top: 0;
        width: 60%;
        height: 362px;
        
    }
    &::after {
        content:"";
        clip-path: polygon(0 0, 100% 0, 100% 100%, 11% 100%);
        background-color:#333333;
        position: absolute;
        top: 0;
        right: 0;   
        width: 56%;
        height: 362px;
    }
   
}

.inform {
    display: flex;
    flex-direction: column;
    padding-top: 100px;
    


  &__title {
    font-weight: 600;
    font-size: 26px;
    line-height: 39px;
    color: $white;
    margin-bottom: 15px;
    font-family: $oswald;
  }

  &__txt {
    max-width: 444px;
    font-size: 18px;
    line-height: 21px;
    color: $white;
    font-family: $roboto;
  }
}

.form {
    position: relative;
    top: -45px;
    width: 413px;
    height: 446px;
    background: $white;
    box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-family: $roboto;
    
    
    &__btn {
        background: $green;
        padding: 13px 0;
        margin: 0 32px 37px;
        color:$white; 
         border: 1px transparent solid;

        &:hover {
            background: $white;
            color: $green;
            border-color: $green;
           
        }
    }
}

.input {
    padding: 23px 30px 40px 33px;
    height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    
    
}

.input-data {
    display: flex;
    
    

    &__item {
    
        &--flex {
                max-width: 160px;
                &:first-child {
                    margin-right: 30px;
                }
                
        }
    }

    &__form {
        width: 100%;
        height: 38px;
    }

    
}

@include media-max($tablet){
    .form-screen {
        height: 315px;
        display: flex;
        align-items: center;
        &::before {
            height: 315px;
        }

        &::after {
            height: 315px;
            width: 61%;
        }
    }

    

    .form {
        max-width: 359px;
        top: 20px;
        flex-shrink: 0;
    }

    .input {
        padding-left: 22px;
        padding-right: 18px;
    }
}


@include media-max(605px) {
    .form-screen {
        margin-bottom: 385px;
    }
    .inform {
        padding-top: 0;
    }
    
    .form {
        position: absolute;
        top: 160px;
        right: max(70px, (100% - 605px) / 2);
        max-width: 400px;
        
    }

}

@include media-max(530px) {
     
    .form {
        
        left: max(15px, (100% - 530px) / 2);
        max-width: 340px;
        top: 200px;
    }

}



@include media-max($mobile) {
    .form-screen {
        margin-bottom: 385px;
        

        &__wrapper {
            flex-direction: column;
            justify-content: center;
        }
    }

    .inform {
        width: 300px;
        padding-top: 0;
    }

    .form {
        position: absolute;
        max-width: 340px;
        top: 260px;
        left: 10px;
    }
    .input {
        padding-left: 18px;
        padding-right: 15px;

        &-data {
            &__form {
                margin-top: 3px;
            }
        }
    }
}