.info {
    background-color: $white;
    padding: 85px 0 305px;
    &__title {
        font-weight: 600;
        font-size: 26px;
        line-height: 39px;
        color: $black;
        margin-bottom: 49px;
        font-family: $oswald;
    }
}

.info-tabs {
    position: relative;
    
    
    &__menu {
        font-family: $roboto;
        background-color: $gray;
        padding: 16px 230px 15px 32px;
        max-width: 350px;
        font-size: 20px;
        line-height: 23px;
        font-weight: 600;

        &:not(:last-child) {
            margin-bottom: 10px;
        }
        &:hover{
            padding: 14px 230px 13px 30px;
            background-color: $white;
            border: 2px solid #01793B;
            
        }
        &--active {
            padding: 14px 230px 13px 30px;
            background-color: $white;
            border: 2px solid #01793B;
        }

    }
    &__item {
        padding: 30px 30px 216px;
        max-width: 730px;
        background: $gray;
        position: absolute;
        right: 0;
        top: 0;
        display: none;
        margin-left: max(150px, (100% - 1110px) / 2);
        &--active {
            display: block;
        }
 
    }

    &__item-title {
        margin-bottom: 15px;
        font-family: $roboto;
        font-weight: 700;
    }
    &__item-txt {
        font-size: 18px;
        line-height: 21px;
        font-family: $roboto;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }
}


@include media-max($tablet){ 
    .info {
        padding-bottom: 85px;
        &__title {
            text-align: center;
        }

        &-tabs {

            &__menu {
                max-width: 100%;
                
                
            }

            &__item {
                position: relative;
                padding-bottom: 30px;
                margin-left: 0;
                &:not(:last-child) {
                    margin-bottom: 10px;
                }
            }
        }
    }


}

@include media-max($mobile) {
    .info {
        padding-bottom: 55px;
        
        &__title {
            max-width: 300px;
            margin: 0 auto 20px;
        }

    }
}
