.partners {
    padding: 80px 0 103px;

    &__title {
        font-weight: 600;
        font-size: 26px;
        line-height: 39px;
        color: #444444;
        margin-bottom: 30px;
        font-family: $oswald;
    }

}

.partners-block {
    display: flex;
    flex-wrap: wrap;
    max-width: 100%;
    justify-content: flex-start;
    &__item {
        width: 65px;
        height: 65px;
        display: flex;
        justify-content: center;
        flex-basis: 8.3%;
        &:not(:nth-child(12n)) {
            margin-bottom: 15px;
        }
    }

}


@include media-max($tablet){
    .partners-block {
        
        &__item {
            flex-basis: 16.6%;
            &:not(:nth-child(6n)) {
                
                margin-bottom: 25px;
            }

            &:nth-child(6n) {
                margin-right: 0;
                margin-bottom: 0;
            }
        }
    }

}

@include media-max($mobile){
    .partners{
        padding: 30px 0 30px;

        &-block{
            &__item {
                width: 42px;
                height: 42px;
            }
        }
    }
}